@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css?family=Pirata+One|Bilbo+Swash+Caps&display=swap');
@import url('https://fonts.googleapis.com/css?family=MedievalSharp&display=swap');
@import url('https://fonts.googleapis.com/css?family=Bad+Script&display=swap');

@font-face {
    font-family: nodesto;
    src: url("fonts/nodesto-caps-condensed.otf") format("opentype");
}

body {
  /*background-image: url("/images/background-smaller.jpg");*/
  /*background-size: 100% auto;*/
  /*background-repeat: repeat-y;*/
  background-color: #191c2b;
  color: lightgray;
}

.App {
  text-align: center;
  max-width: 992px;
  min-width: 360px;
  padding-left: 12%;
  padding-right: 12%;
}

.App-link {
  color: #61dafb;
}

.App-header {
  width: 100%;
}

.App-logo {
  width: 100%;
  max-width: 496px;
  margin-top: 30px;
}

.img-shadow {
  -webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
  -moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
  box-shadow: 0px 0px 5px 0px rgba(0,0,0,0.5);
}

.top-spacing {
  margin-top: 3vh;
}

.top-spacing-4 {
  margin-top: 4vh;
}

.top-spacing-6 {
  margin-top: 6vh;
}

.bottom-spacing {
  margin-bottom: 3vh;
}

.title {
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  color: #955E42;
}

.subtitle {
  font-weight: 200;
}

.bottom-divider {
  margin: 10% 0 2% 0;
  border-bottom: 1px solid rgba(211,211,211,0.25);
}

.middle-divider {
  margin: 3% 35% 3% 35%;
  border-bottom: 1px solid rgba(211,211,211,0.25);
}

p {
  font-size: 16px;
  max-width: 800px;
}

h4 {
  margin-bottom: 3vh;
  font-family: 'nodesto';
  color: white;
  font-size: 3rem;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
}

.footer {
  color: #b2d0d0;
}

.character-portrait-grid {
  max-width: 90%;
  margin: 0 auto;
}

.character-portrait-container {
  padding-bottom: 15px;
  padding-top: 15px;
  width: 20vh;
}

.character-portrait {
  object-fit: cover;
  object-position: 50% 0;
  width: 100px;
  height: 100px;
}

a {
  color: #b1cfcd;
  display: block;
  margin-top: 1vh;
  font-weight: 500;
  font-size: 18px;
}

.media-container {
  max-width: 768px;
  width: 90%;
}

.media {
  max-width: 768px;
  /*width: 90%;*/
  max-height: 240px;
  margin: 0 auto;
  padding: 2vh;
  border: 1px solid rgba(0,0,0,0.12);
  border-radius: 8px 8px 8px 8px;
  background-color: #e7e3dc;
}

.media-body {
  width: 60%;
}

.media-image {
  margin-left: 20px;
  border: 1px solid #ececec;
  max-width: 50%;
  max-height: 220px;
}

.return-link {
  margin-top: -15px;
  margin-bottom: 25px;
}

.session {
  margin-top: 3%;
}

.icon-container {
  color: #cecece;
}

.fa-times {
  cursor: pointer;
}

.session-img {
  max-width: 600px;
  width: 100%;
  margin-top: 15px;
  margin-bottom: 25px;
}

div#parchment-container {
  position: relative;
  width: 100%;
  height: auto;
  font-family: 'Bad Script';
  margin-bottom: 8vh;
}

div#parchment-container p {
  font-size: 1.25em;
  color: #7F3300;
  margin-top: .75em;
  text-align: justify;
}

div#parchment-container p#parchment-title {
  font-size: 1.75em;
  font-weight: bold;
  text-align: center;
}

div#parchment-container p#parchment-subtitle {
  font-size: 1.5em;
  text-align: center;
}

#parchment {
  position: absolute;
  display: flex;
  width: 100%;
  /* center page with absolute position */
  top: 0%; left: 50%; transform: translate(-50%, 0);
  margin: 2em 0;
  padding: 4em;
  box-shadow: 2px 3px 20px black, 0 0 125px #8f5922 inset;
  background: #fffef0;
  /* v2.2 : Noise added for a vellum paper effect */
  background-image: url(data:image/png;base64,);
}

@media screen and (-webkit-min-device-pixel-ratio:0)
  and (min-resolution:.001dpcm) {
    #parchment {
      /* v2.1 : borders effect with SVG : try to play with scale to change them */
      filter: url(#wavy2);
    } 
}

#contain {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: auto;
  margin: 0 auto;
  padding: 4em;
}

svg {
